header {
	height: 100vh;
	padding-top: 7rem;
	overflow: hidden;
}

.header__container {
	text-align: center;
	height: 100%;
	position: relative;
}

/* ========== CTA =========*/
.cta {
	margin-top:  2.5rem;
	display: flex;
	gap: 1.2rem;
	justify-content: center;
}

/* ============= HEADER SOCIALS ============*/
.header__socials {
	font-size: 1.8rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.8;
	position: absolute;
	left: 0;
	bottom: 3rem;
}

.header__socials::after {
	content: '';
	width: 1px;
	height: 2rem;
	background: var(--color-primary);
}

/* ========== ME =========== */
.me {
	background: linear-gradient(var(--color-primary), transparent);
	width: 22rem;
	height: fit-content;
	position: absolute;
	left: calc(50% - 11rem);
	margin-top: 4rem;
	border-radius: 5rem;
	overflow: hidden;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.8);
}

/* =========== SCROLL DOWN ========== */
.scroll__down {
	position: absolute;
	right: -2.3rem;
	bottom: 5rem;
	transform: rotate(90deg);
	font-weight: 300;
	font-size: 1.8rem;
}

/* =========== MEDIA QUERIES (MEDIUM DEVICES) =========== */
@media screen and (max-width: 1024px) {
	header {
		height: 68vh;
	}
}


/* =========== MEDIA QUERIES (SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
	header {
		height: 100vh;
	}

	.header__socials,
	.scroll__down {
		display: none;
	}
}